<template>
  <div class="slider-wrapper">
    <div class="slider-body" :style="sliderBodyStyle">
      <img class="slider-item" v-for="(item,index) in realList" :src="item.picUrl" :style="sliderItemStyle" :key="index" @touchstart="sliderTouchStart" @touchmove="sliderTouchMove" @touchend="sliderTouchEnd">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      clientWidth: document.body.clientWidth,
      sliderIndex: 1,
      timer: null,
      sliderBody: null,
      startPos: {
        x: 0,
        y: 0
      },
      currentPos: {
        x: 0,
        y: 0
      },
      prevPos: {
        x: 0,
        y: 0
      }
    };
  },
  props: {
    sliderList: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    realList() {
      let arr = this.sliderList;
      let arrFirst = Object.assign({}, arr[0]);
      let arrLast = Object.assign({}, arr[arr.length - 1]);
      console.log(arrFirst);
      console.log(arrLast);
      arr.unshift(arrLast);
      arr.push(arrFirst);
      return arr;
    },
    sliderBodyStyle() {
      let picNum = this.realList.length;
      return {
        width: picNum * 100 + "%",
        left: this.sliderLeft + "px",
        height: this.clientWidth / 2.5 + "px",
        transition: "0s",
        "-webkit-transition": "0s"
      };
    },
    sliderItemStyle() {
      return {
        width: this.clientWidth + "px",
        height: "100%"
      };
    },
    sliderLeft() {
      return -this.sliderIndex * this.clientWidth;
    }
  },
  mounted() {
    this.runSlider();
    this.sliderBody = document.getElementsByClassName("slider-body")[0];
    window.onresize = () => {
      this.clientWidth = document.body.clientWidth;
      this.setTransInterval(0);
      this.sliderBodyStyle.left = -this.clientWidth * this.sliderIndex + "px";
    };
  },
  methods: {
    runSlider() {
      this.timer = setInterval(() => {
        this.sliderIndex += 1;
        if (this.sliderIndex === this.realList.length) {
          clearInterval(this.timer);
          this.timer = null;
          this.setTransInterval(0);
          this.sliderIndex = 1;
          this.sliderBody.style.left = -this.clientWidth + "px";
          setTimeout(() => {
            this.setTransInterval(0.5);
            this.sliderIndex = 2;
            this.sliderBodyStyle.left =
              -this.clientWidth * this.sliderIndex + "px";
            this.runSlider();
          }, 10);
        } else {
          this.setTransInterval(0.5);
        }
      }, 2000);
    },
    setTransInterval(range) {
      this.sliderBody.style.transition = range + "s";
      this.sliderBody.style["-webkit-transition"] = range + "s";
    },
    sliderTouchStart(event) {
      console.log("start");
      clearInterval(this.timer);
      this.timer = null;
      let target = event.touches[0];
      this.startPos.x = target.pageX;
      this.startPos.y = target.pageY;
      this.prevPos.x = this.startPos.x;
      this.prevPos.y = this.startPos.y;
    },
    sliderTouchMove(event) {
      console.log("move");
      let target = event.touches[0];
      this.currentPos.x = target.pageX;
      this.currentPos.y = target.pageY;
      let moveDis = this.currentPos.x - this.prevPos.x;
      if (moveDis < 0 && this.sliderIndex === this.realList.length - 1) {
        this.setTransInterval(0);
        this.sliderIndex = 1;
        this.sliderBody.style.left =
          -this.clientWidth * this.sliderIndex + "px";
        // this.prevPos.x = 0;
        setTimeout(() => {
          let thisLeftNum = parseInt(this.sliderBody.style.left, 10);
          this.sliderBody.style.left = thisLeftNum + moveDis + "px";
          this.prevPos.x = this.currentPos.x;
          this.prevPos.y = this.currentPos.y;
        }, 10);
      } else if (moveDis > 0 && this.sliderIndex === 0) {
        this.setTransInterval(0);
        this.sliderIndex = this.realList.length - 2;
        this.sliderBody.style.left =
          -this.clientWidth * this.sliderIndex + "px";
        setTimeout(() => {
          let thisLeftNum = parseInt(this.sliderBody.style.left, 10);
          this.sliderBody.style.left = thisLeftNum + moveDis + "px";
          this.prevPos.x = this.currentPos.x;
          this.prevPos.y = this.currentPos.y;
        }, 10);
      } else {
        let thisLeftNum = parseInt(this.sliderBody.style.left, 10);
        this.sliderBody.style.left = thisLeftNum + moveDis + "px";
        this.prevPos.x = this.currentPos.x;
        this.prevPos.y = this.currentPos.y;
      }
    },
    sliderTouchEnd(event) {
      console.log("end");
      let target = event.changedTouches[0];
      this.currentPos.x = target.pageX;
      this.currentPos.y = target.pageY;
      let moveDis = this.currentPos.x - this.startPos.x;
      if (moveDis > 0) this.sliderIndex -= 1;
      if (moveDis < 0) this.sliderIndex += 1;
      this.setTransInterval(0.5);
      this.runSlider();
    }
  }
};
</script>

<style lang="stylus">
.slider-wrapper
  height: auto
.slider-body
  position: relative
  // height: 100%
  overflow: hidden
  transition: left 0.5s, width 0.5s
  -webkit-transition: 0.5s, width 0.5s
.slider-item
  display: inline-block
  // height: 100%
  font-size: 0
  background-size: 100% auto
  background-repeat: no-repeat
.slider-bars
  position: absolute
  left: 50%
  transform: translateX(-50%)
  bottom: 12px
  height: 20px
  font-size: 0px
.slider-bar
  display: inline-block
  box-sizing: content-box
  margin: 4px
  width: 12px
  height: 12px
  border-radius: 50%
  background-color: #a0a0a0
.slider-bar-active
  background-color: #eaeaea
</style>


